body {
  background-color: #f8f8f8;
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
}

p {
  font-size: 2rem;
}

[class$=contenedor],
.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

.seccion {
  padding: 8rem 0;
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.5;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.2rem;
}

h3 {
  font-size: 2.4rem;
}

[class$=heading] {
  margin: 2rem 0;
}

a {
  text-decoration: none;
}

.btn {
  background-color: #FFFFFF;
  padding: 2rem 2rem;
  color: #000;
  margin-top: 2rem;
  font-size: 2.2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  transition: background-color;
  transition-duration: 300ms;
}
@media (min-width: 768px) {
  .btn {
    width: auto;
  }
}
.btn:hover {
  background-color: #e6e6e6;
}

.boton-negro {
  background-color: #000;
  padding: 2rem 2rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2.2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  transition: background-color;
  transition-duration: 300ms;
}
@media (min-width: 768px) {
  .boton-negro {
    width: auto;
  }
}
.boton-negro:hover {
  background-color: black;
}

.header {
  padding-top: 5rem;
}
.header__grid {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
@media (min-width: 768px) {
  .header__grid {
    flex-direction: row;
    justify-content: space-between;
  }
}

.navegacion {
  background-color: #8ed3cc;
  padding: 0.3rem 0;
  margin: 2rem 0;
}
.navegacion__border {
  border-block: 2px dashed #FFFFFF;
  padding: 1.7rem 0;
}
.navegacion__contenedor {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .navegacion__contenedor {
    flex-direction: row;
    justify-content: space-between;
  }
}
.navegacion__enlace {
  color: #FFFFFF;
  font-size: 2.4rem;
  margin-bottom: 1.2rem;
}
.navegacion__enlace:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .navegacion__enlace {
    margin-bottom: 0;
  }
}

.sociales {
  margin: 5rem 0;
  display: flex;
}
@media (min-width: 768px) {
  .sociales {
    margin: 0;
  }
}
.sociales__enlace::before {
  content: "";
  padding: 2rem;
  display: inline-block;
  margin: 0 0.5rem;
}
.sociales__enlace[href*="facebook.com"]::before {
  background-image: url("../img/icono_facebook.svg");
}
.sociales__enlace[href*="instagram.com"]::before {
  background-image: url("../img/icono_instagram.svg");
}
.sociales__enlace[href*="twitter.com"]::before {
  background-image: url("../img/icono_twitter.svg");
}
.sociales__enlace[href*="pinterest.com"]::before {
  background-image: url("../img/icono_pinterest.svg");
}
.sociales__enlace[href*="youtube.com"]::before {
  background-image: url("../img/icono_youtube.svg");
}
.sociales__enlace[href*="tiktok.com"]::before {
  background-image: url("../img/icono_tiktok.svg");
}
.sociales__accesible {
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  display: block;
  border: 0;
  clip: 0, 0, 0, 0;
}

.bienvenida {
  text-align: center;
}
.bienvenida__heading {
  text-align: center;
  color: #e082a8;
  font-family: "Italianno", sans-serif;
  line-height: 0.8;
  text-transform: lowercase;
  font-weight: 400;
  font-size: 4rem;
}
.bienvenida__heading span {
  color: #000;
  display: block;
  text-transform: uppercase;
  font-family: "Lato", sans-serif;
}
.bienvenida__texto {
  font-size: 2.6rem;
  color: #979797;
  max-width: 50rem;
  margin: 4rem auto 0;
  line-height: 1.8;
}

@media (min-width: 768px) {
  .cards__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.card {
  max-width: 35rem;
  margin: 0 auto 2rem;
}
.card:last-of-type {
  margin-bottom: 0;
}
.card:last-of-type .card__enlace {
  padding: 1rem;
}
@media (min-width: 768px) {
  .card {
    max-width: unset;
    margin: 0;
  }
}
.card__imagen {
  width: 100%;
  border: 0.5rem solid #FFFFFF;
  transition: transform 0.3s ease;
}
.card__imagen:hover {
  transform: rotate(4deg) scale(1.3);
}
.card__contenido {
  margin: 0 auto;
  max-width: 90%;
  background-color: #FFFFFF;
  transform: translateY(-7rem);
}
.card__heading {
  text-align: center;
  color: #e082a8;
  font-family: "Italianno", sans-serif;
  line-height: 0.8;
  text-transform: lowercase;
  font-weight: 400;
  font-size: 4rem;
  padding-top: 3rem;
}
.card__heading span {
  color: #000;
  display: block;
  text-transform: uppercase;
  font-family: "Lato", sans-serif;
}
.card__enlace {
  background-color: #e082a8;
  padding: 2rem 2rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2.2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  transition: background-color;
  transition-duration: 300ms;
  display: block;
}
@media (min-width: 768px) {
  .card__enlace {
    width: auto;
  }
}
.card__enlace:hover {
  background-color: #d6598c;
}

.horario {
  background-image: linear-gradient(to bottom, #8ed3cc 0%, #8ed3cc 50%, transparent 50%, transparent 100%), url("../img/bg_horarios.jpg");
  background-repeat: no-repeat;
  background-position: center, bottom center;
  background-size: 100%, 1200%;
  color: #FFFFFF;
}
@media (min-width: 200px) {
  .horario {
    background-size: 100%, 700%;
  }
}
@media (min-width: 480px) {
  .horario {
    background-size: 100%, 250%;
  }
}
@media (min-width: 600px) {
  .horario {
    background-size: 100%, 180%;
  }
}
@media (min-width: 768px) {
  .horario {
    background-image: linear-gradient(to right, #8ed3cc 0%, #8ed3cc 50%, transparent 50%, transparent 100%), url("../img/bg_horarios.jpg");
    background-position: center, top right;
    background-size: 100%, cover;
  }
}
.horario__grid {
  display: grid;
  grid-template-rows: repeat(2, 1fr);
}
@media (min-width: 768px) {
  .horario__grid {
    grid-template-rows: unset;
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.horario__contenido {
  padding: 5rem 0;
}
.horario__heading {
  text-transform: uppercase;
  text-align: center;
  font-weight: 400;
  font-size: 4rem;
}
.horario__texto {
  font-size: 2.2rem;
  line-height: 1.8;
  text-align: center;
}

.table {
  width: 100%;
  border: 0.5rem solid #e082a8;
  text-align: center;
  border-collapse: collapse;
}
.table__thead {
  background-color: #e082a8;
  font-size: 2.5rem;
}
.table__thead .table__tr:hover {
  background-color: #e082a8;
}
.table__th {
  padding: 2rem;
}
.table__td {
  padding: 1rem;
  font-size: 2.2rem;
}
.table__tr:hover {
  background-color: #69c5bb;
}

.productos__heading {
  text-align: center;
  color: #e082a8;
  font-family: "Italianno", sans-serif;
  line-height: 0.8;
  text-transform: lowercase;
  font-weight: 400;
  font-size: 4rem;
  margin-bottom: 5rem;
}
.productos__heading span {
  color: #000;
  display: block;
  text-transform: uppercase;
  font-family: "Lato", sans-serif;
}
.productos__grid {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .productos__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .productos__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(4, 1fr);
  }
}

.producto__contenido {
  background-color: #FFFFFF;
  padding: 2rem;
}
.producto__mask {
  overflow: hidden;
}
.producto__imagen {
  width: 100%;
  transition: transform 0.3s ease;
}
.producto__imagen:hover {
  transform: scale(1.2);
}
.producto__nombre {
  text-transform: uppercase;
  text-align: center;
  font-weight: 400;
  font-size: 4rem;
  font-size: 2.4rem;
  color: #e082a8;
}
.producto__descripcion {
  text-transform: uppercase;
  font-size: 1.6rem;
  line-height: 1.8;
}
.producto__precio {
  text-align: center;
  color: #979797;
  font-size: 2.8rem;
  margin: 0;
}

.cita {
  background-image: url("../img/bg_citas.jpg");
  background-position: center center;
  background-size: cover;
  padding: 5rem 0;
  text-align: center;
  background-attachment: fixed;
}
.cita__heading {
  text-transform: uppercase;
  text-align: center;
  font-weight: 400;
  font-size: 4rem;
  color: #FFFFFF;
  font-size: 6rem;
}
.cita__texto {
  max-width: 60rem;
  margin: 0 auto;
  color: #FFFFFF;
  line-height: 2;
}
.cita__enlace {
  background-color: #8ed3cc;
  padding: 2rem 2rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2.2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  transition: background-color;
  transition-duration: 300ms;
  display: block;
  width: auto;
  margin-top: 3rem;
}
@media (min-width: 768px) {
  .cita__enlace {
    width: auto;
  }
}
.cita__enlace:hover {
  background-color: #69c5bb;
}
@media (min-width: 480px) {
  .cita__enlace {
    margin: 3rem auto 0;
    width: 50%;
  }
}
@media (min-width: 768px) {
  .cita__enlace {
    width: 30%;
  }
}

.swiper {
  max-width: 120rem;
  width: 80%;
  height: 100%;
}
.swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #f8f8f8;
  background-position: center;
  background-size: cover;
  /* Center slide text vertically */
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
}
.swiper-image {
  display: block;
  width: 100%;
  height: 100%;
}
.swiper-pagination-bullet {
  background-color: #FFFFFF;
  opacity: unset;
}
.swiper-pagination-bullet-active {
  background-color: #46b5a9;
}
.swiper-button-next:after, .swiper-button-prev:after {
  color: #FFFFFF;
}

.footer__grid {
  padding-bottom: 6rem;
}
@media (min-width: 768px) {
  .footer__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.footer__widget {
  text-align: center;
  margin-bottom: 5rem;
}
.footer__widget:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .footer__widget {
    margin-bottom: 0;
  }
}
.footer__widget:nth-child(3) {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.footer__heading {
  text-transform: uppercase;
  text-align: center;
  font-weight: 400;
  font-size: 4rem;
  font-size: 3.4rem;
  color: #46b5a9;
}
.footer__texto {
  line-height: 1.8;
}
.footer__copyright {
  text-align: center;
  color: #ca326f;
  max-width: 120rem;
  margin: 0 auto;
  font-size: 1.6rem;
  border-top: 1px solid #b1b1b1;
  padding: 1rem 0;
}
.footer .sociales {
  margin: 2rem 0;
}